<template>
<el-card>
    <div slot="header">
        <span>角色查询</span>
    </div>

    <div>
        <!-- 搜索 -->
        <div>
            <el-form :model="objSearch" :rules="rules" ref="formSearch" label-width="100px">
                <el-row :gutter="20">
                    <el-col :span="6">
                        <el-form-item label="渠道ID">
                            <el-input v-model="objSearch.channel" placeholder="请输入渠道ID"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="服务器ID" prop="serverId">
                            <el-input v-model="objSearch.serverId" placeholder="请输入服务器ID"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="类型">
                            <el-select v-model="objSearch.type" placeholder="请选择类型">
                                <el-option label="角色ID" value="1"></el-option>
                                <el-option label="角色名" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="类型值">
                            <el-input v-model="objSearch.param_value" placeholder="请输入类型值"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="查询模式">
                            <el-select v-model="objSearch.selectModel" placeholder="请选择查询模式">
                                <el-option label="精确查询" value="1"></el-option>
                                <el-option label="模糊查询" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-button type="primary" @click="handleSearch('formSearch')">搜索</el-button>
                        <el-button type="primary" @click="handleReset">重置</el-button>
                    </el-col>
                </el-row>
            </el-form>
        </div>
        <!-- 表格 -->
        <div>
            <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%" max-height="600">
                <template v-for="(item,idx) in aTableColumn">
                    <el-table-column :key="idx" :prop="item.field" :label="item.tableHead" min-width="120" show-overflow-tooltip></el-table-column>
                </template>
                <el-table-column label="操作" min-width="150" fixed="right">
                    <template slot-scope="scope">
                        <el-button @click.native.prevent="handleClickDetail(scope.row)" type="text" size="small">详细</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</el-card>
</template>

<script>
export default {
    name: "create-id",
    data() {
        return {
            objSearch: {
                channel: "",
                serverId: "",
                type: "",
                param_value: "",
                selectModel: ""
            },
            rules: {
                serverId: [{
                    required: true,
                    message: "请输入",
                    trigger: "blur"
                }]
            },
            aTableData: [],
            aTableColumn: []
        };
    },
    methods: {
        // 重置
        handleReset() {
            this.objSearch = {};
        },
        //   搜索
        handleSearch(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.fetchRole();
                } else {
                    return false;
                }
            });
        },
        // 详情
        handleClickDetail(params) {
            this.$router.push({
                name: "role_detail",
                query: {
                    pid: params.pid,
                    serverId: params.serverId
                }
            });
        },
        // 列表
        fetchRole() {
            let params = JSON.parse(JSON.stringify(this.objSearch));
            this.$fetch.post(API.role.select_player, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let data = res.data;
                    this.aTableData = data.fieldList;
                    this.aTableColumn = data.headList;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        // this.fetchRole();
    }
};
</script>

<style lang="stylus" scoped></style>
